#content {	
	position: absolute;
	left: 0;
	top: 3rem;
	right: 0;
	bottom: 0;
	background: white;
	transition: all 0.5s;

	&.injecting {
		#tree,
		#tree * {
			opacity: 1;

			&:before {
				display: none;
			}
		}

		#main-content {
			@include screen-size(small) {
				opacity: 1;
			}
		}
	}

	#tree {
		padding: 6rem 3% 2rem;
		overflow: auto;
		width: 33.33332%;
		@include box-sizing;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		border-right: 1px solid silver;
		background: white;

		@include screen-size(small) {
			padding-top: 2rem;
			width: 100%;
			z-index: 11;
			transition: all 0.5s;

			.sidebar-off & {
				left: -100%;
				opacity: 0;
			}
		}
	}

	#main-content {
		@include box-sizing;
		position: relative;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;

		&.variation-risk-assessment {
			@include screen-size(medium-large) {
				left: 33.33332%;
			}
		}

		#content-pane {
			background: white;
			overflow: auto;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			@include box-sizing;

			.touchevents & {
				overflow-y: scroll; /* has to be scroll, not auto */
				-webkit-overflow-scrolling: touch;
			}

			@include screen-size(small) {
				padding: 30px $padding-small-screen 0; // Watch bottom padding in IE11
			}

			@include screen-size(medium-large) {
				font-size: 120%;
				padding: 6rem 6% 0; // Watch bottom padding in IE11
			}

			&:after {
				// This construct is in place to guarantee a bottom padding in IE11. IE will under certain circumstances ignore the bottom padding in combination with overflow: auto. The after element replace the bottom padding.

				display: block;
				content: ' ';
				width: 100%;
				height: 10rem;
				background: rgba(0,0,0,0);
				clear: both;
			}

		}
	}
}

@include screen-size(medium) {
	.osc-s-on #content {
		opacity: 0.5;
		pointer-events: none;
	}
}