table {
	width: 100%;
	// font-size: 0.8em;
	border-top: 1px solid $colour-accent;

	&.hd {
		font-size: 0.8em;

		th,
		td {
			border-bottom: 1px solid $colour-accent;
			padding: 0.3em 0.5em;
			line-height: 1.3;
		}
	}

	td.actions {
		text-align: right;

		form {
			display: inline;
		}

		a,
		button {
			color: #666;
		}
	}

	th,
	td {
		border-bottom: 1px solid $colour-accent;
		padding: 0.2em 0.8em;
		line-height: 1.5;
	}

	thead {

		th,
		td {
			vertical-align: bottom;
			position: -webkit-sticky;
			position: sticky;
			top: -6rem;
			background: white;
			z-index: 1;
		}
	}

	tbody {
		tr {
			td,
			th {
				vertical-align: top;

				[class^="icon-"]:not(.iconified), 
				[class*=" icon-"]:not(.iconified) {
					display: inline-block;
					position: relative;
					padding-left: 26px;

					&:before {
						position: absolute;
						left: 0;
						top: 50%;
						transform: translateY(-50%);;
					}
				}
			}

			&.archived {
				td {
					&.name a {
						color: gray;

						&:after {
							font-family: fontello;
							content: '#{$glyph-archived}';
							font-size: 0.6em;
							vertical-align: middle;
							padding-left: 0.5em;
							position: relative;
							top: -0.05em;
						}
					}
				}
			}
		}

		// tr:first-child {
		// 	th {
		// 		padding-top: 1.5em;
		// 		font-style: italic;
		// 	}
		// }
	}


	&.diagonal-headers {		

		thead {

			th {
				height: 120px;
				position: relative;
				background: transparent;
				top: 0;

				.label {
					-webkit-transform: rotate(-45deg);
					transform: rotate(-45deg);
					transform-origin: 0 0;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					position: absolute;
					bottom: 0;
					width: 160px;
					// background: white;
					cursor: default;
					transition: width 1s;
				}
			}

			&:hover {
				th {
					.label {
						width: auto;
						background: white;
					}
				}
			}
		}

	}
}