@mixin toolbar-active-state() {
	text-shadow: 0 0 2px $colour-toolbar,
		         0 0 2px $colour-toolbar,
		         0 0 2px $colour-toolbar,
		         0 0 2px $colour-toolbar,
	             0 0 5px white,
	             0 0 6px white,
	             0 0 7px white;
}

#toolbar {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	background-color: $colour-toolbar;
	color: white;

	padding: 0.61rem 2% 0.61rem 2%;

	@include screen-size(small) {
		overflow: hidden;
		padding: 0.61rem 4% 0.61rem 4%;
	}

	&.injecting {
		> * {
			opacity: 1;
		}

		&:before {
			display: none;
		}
	}

	border-bottom: silver 1px solid;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	height: 3rem;

	.warning {
		display: inline-block;
		background-color: $colour-alert;
		padding: 0 0.8em;
		border: rgba(255,255,255,0.6) solid 1px;
		border-radius: 2px;

		a {
			text-decoration: underline;
		}
	}

	> a,
	> button {	
		color: white;
		// padding: 0 0.5em;
		cursor: default;
		display: inline-block;
		line-height: 1.6;
		padding: 0;
		// width: 2rem;
		overflow: hidden;
		display: inline-block;
		float: left;
		height: 1.5em;
		margin-right: 0.8em;

		&:before {
			margin: 0;
			text-align: center;
			width: 100%;
			font-family: fontello;
		}

		@include screen-size(small) {
			margin-bottom: 1em;
		}

		&#toggle-tree {
			&:before {
				content: '#{$glyph-menu-bars}';

				.sidebar-on & {
					@include toolbar-active-state;
				}
			}

			@include screen-size(medium-large) {
				display: none;
			}
		}

		&#toggle-osc {
			&:before {
				content: '#{$glyph-menu}';

				@include screen-size(large) {
					.osc-on & {
						@include toolbar-active-state;
					}	
				}

				@include screen-size(small-medium) {
					.osc-s-on & {
						@include toolbar-active-state;
					}
				}
			}

			@include screen-size(small) {
				display: none;
			}
		}

		&#more-menu {
			&:before {
				content: '#{$glyph-ellipsis}';
			}

			&.active {
				&:before {
					@include toolbar-active-state;
				}
			}
		}
	}

	.user-status,
	.login,
	.registration,
	.help,
	.home,
	#more-menu {
		float: right;
		margin-left: 0.8em;
		margin-right: 0;
	}

}

.osc-off #toolbar #burger {
	// box-shadow: inset 0 0 0.15em rgba(0,0,0,0);

	&:before {
		@include screen-size(large) {
			content: "\e81d";
		}

		@include screen-size(medium) {
			content: "\e81d";
		}

		@include screen-size(small) {
			content: "\e81b";
		}
	}
}

@include screen-size(small) {
	.focus-sidebar {
		#toolbar .icon-left {
			display: none;
		}

		#toolbar #burger {
			display: inline-block;

			&:before {
				font-size: 1em;
				width: 1.6rem;
				line-height: 1.7rem;
				float: left;
			}
		}
	}

	.osc-s-off {
		#toolbar #burger {
			&:before {
				content: "\e812";
			}
		}
	}

	.osc-s-on {
		#toolbar #burger {
			&:before {
				content: "\e81b";
			}
		}
	}

