.button-bar {
	margin-bottom: 1em;
	@include clear;

	.pat-button {
		&.float-after {
			float: right;
			margin-right: 0;
			margin-left: 8px;
		}
	}

	&#nav-bar {

		.pat-button.continue,
		.pat-button.skip,
		.pat-button.float-after {
			float: right;
			margin-right: 0;
			margin-left: 8px;
		}
		
		@include screen-size(small) {
			position: fixed;
			bottom: 0;
			margin: 0;
			left: 4%;
			right: 4%;
			background: white;
			padding: 15px 0 0.5em;
			@include clear;
			z-index: 10;
			border-top: 1px solid black;
			transition: transform 1s;
			font-size: 120%;

			.focus-sidebar & {
				transform: translateY(100%);
			}

			.pat-button {
				padding: 0 1em;
				font-size: 0.7em;
				height: 35px;
				line-height: 36px;
				position: relative;
				max-width: 100%;
				margin-bottom: 0.5em;
			}
		}
		@include screen-size(medium-large) {
			position: absolute;
			bottom: 0;
			margin: 0 1rem; // Margin left and right required to not cover scrollbar in IE.
			left: 0;
			right: 0;
			z-index: 10;
			padding: 1em 1rem;

			&:after {
				position: absolute;
				right: 0;
				left: 0;
				bottom: 0;

/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&0+0,1+58 */
background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 58%, rgba(255,255,255,1) 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 58%,rgba(255,255,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 58%,rgba(255,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */

				height: 12rem;
				content: ' ';
				display: block;
				z-index: -1;
				pointer-events: none;
			}
		}

		.pat-button {
			@include screen-size(medium-large) {
				line-height: 48px;
				height: 49px;
				padding: 0 45px;
			}

			&.back {
				width: 35px;
				color: transparent;
				text-align: center;
				overflow: hidden;
				padding: 0;
				position: relative;

				&:before {
					font-family: fontello;
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					content: '#{$glyph-left}';
					font-size: 28px;
					@include screen-size(medium-large) {
						line-height: 48px;
					}
				}
			}

			&.continue {
				background-color: $colour-button-continue;
				color: $colour-button-continue-text;
			}
		}
	}
}

.pat-checklist.pat-clone + .button-bar.cloning {
	transition: padding-left 0.4s;
	margin-top: -0.8em;
}

.pat-checklist.pat-clone.has-value + .button-bar.cloning {
	padding-left: 1.5em;
	margin-top: -0.8em;
}