.pat-button {
	font-style: normal;
	font-weight: normal;
	
	&.facebook {
		color: white;
		background-color: #3b5998;

		&:before {
			color: white;
		}
	}

	&.twitter {
		color: white;
		background-color: #00aced;

		&:before {
			color: white;
		}
	}

	&.email {
		color: black;
		background-color: white;
	}

	&:focus {
		border: none;
		box-shadow: 0 0 10px $colour-accent;
		outline: 1px solid white;
	}

	&.small,	
	.button-bar &.small,
	.tooltip-container #canvas-toolbar & {
		line-height: 28px;
		height: 29px;
		padding: 0 0.8em;
		font-size: 0.8em;
	}
}