html {
	height: 100%;
	width: 100%;
	overflow: hidden;

	&.print {
		overflow: visible;
		height: auto;
	}

	&.iframe {
		overflow: visible;
		height: auto;
	}
}

body {
    color: $body-font-color;
    font-family: $body-font-family;
    font-weight: $body-font-weight;
    font-style: $body-font-style;
    line-height: $base-line-height;
    font-size: $base-font-size;
    background: $body-bg;
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;

	[hidden="hidden"],
	[hidden],
	[type="hidden"] {
		display: none !important;
	}

	// &.help-screen-render {
	// 	> * {
	// 		animation: fadeIn 1s;
	// 	}

	// 	&:not(.patterns-loaded) {
	// 		> * {
	// 			display: none;
	// 		}
	// 	}
	// }

	&.print,
    &.iframe {
		overflow: visible;
		height: auto;
		position: static;
	}

	.screen-only {
		@media print {
			display: none !important;
		}
	}
}