/*
This code is loosely based on Zurb Foundation's grid framework.
*/

.row,
.columns {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

/* @group The Grid */

.row {
	margin: 0 -15px;
}

.row .row {
	width: auto;
	max-width: none;
	min-width: 0;
	margin: 0 -15px;
}

.row.collapse .column, .row.collapse .columns {
	padding: 0;
}

.row .row {
	width: auto;
	max-width: none;
	min-width: 0;
	margin: 0 -15px;
}

.row .row.collapse {
	margin: 0;
}

.column, 
.columns {
	float: left;
	min-height: 1px;
	padding: 0 15px;
	position: relative;
}

.column.centered, .columns.centered {
	float: none;
	margin: 0 auto;
}

[class*="column"] + [class*="column"]:last-child {
	float: right;
}

[class*="column"] + [class*="column"].end {
	float: left;
}

.one, .row .one {
	width: 8.33333%;
}

.two, .row .two {
	width: 16.66667%;
}

.three, .row .three {
	width: 25%;
}

.four, .row .four {
	width: 33.33333%;
}

.five, .row .five {
	width: 41.66667%;
}

.six, .row .six {
	width: 50%;
}

.seven, .row .seven {
	width: 58.33333%;
}

.eight, .row .eight {
	width: 66.66667%;
}

.nine, .row .nine {
	width: 75%;
}

.ten, .row .ten {
	width: 83.33333%;
}

.eleven, .row .eleven {
	width: 91.66667%;
}

.twelve, .row .twelve {
	width: 100%;
}

.row .offset-by-one {
	margin-left: 8.33333%;
}

.row .offset-by-two {
	margin-left: 16.66667%;
}

.row .offset-by-three {
	margin-left: 25%;
}

.row .offset-by-four {
