@charset "UTF-8";

.link-list-title {
  margin: 0;

  & + .link-list {
    margin-top: 0.4em;
  }
}

ul.links,
.link-list {
  display: block;
  margin-bottom: 2rem;
  line-height: 1.3;

  @include screen-size(large) {
    &.col-2 {
      column-count: 2;
      overflow: hidden;
    }

    &.col-3 {
      column-count: 3;
      overflow: hidden;
    }

    &.col-4 {
      column-count: 4;
      overflow: hidden;
    }
  }

  @include screen-size(medium) {
    &.col-2 {
      column-count: 2;
      overflow: hidden;
    }

    &.col-3 {
      column-count: 3;
      overflow: hidden;
    }

    &.col-4 {
      column-count: 3;
      overflow: hidden;
    }
  }

  li {
    display: block;
    padding: 0;
    break-inside: avoid;

    &:before {
      display: none;
    }

    a {
      display: block;
      padding: 0.3em 0 0.3em 1em;
      position: relative;

      &.archived {
        padding-right: 28px;
        color: gray;
        display: inline-block;

        &:after {
          position: absolute;
          right: 4px;
          top: 50%;
          transform: translateY(-50%);
          font-family: fontello;
          content: '#{$glyph-archived}';
          font-size: 14px;
          color: gray;
        }
      }

      &:hover {
        text-decoration: underline;

        &:before {
          text-decoration: none;
        }
      }

      &:not([class*="icon-"]) {

        &:before {
          position: absolute;
          font-family: fontello;
          content: '#{$glyph-list-item}';
          left: 0;
        }
      }
    }
  }

}