.risk-module {
	border-top: 1px silver solid;
	border-bottom: 1px silver solid;
	margin-bottom: 1em;
	padding: 0.5em 0;
	font-size: 1em;

	.image-uploader {
		min-height: 8em;

		img {
			margin-top: 0.2em;
		}
	}

	h4 {
		font-weight: bold;
		
	}

	.measures {

		&+.button-bar {
			margin-bottom: 1em;
		}
	}

	> .collapsible-closed,
	> .collapsible-open {
		margin: 0;
		font-weight: bold;
		font-size: 1em;
		line-height: 1.4;
		position: relative;
		padding-left: 30px;

		&:before {
			top: 50%;
			position: absolute;
			left: 1px;
			height: 22px;
			margin: -11px 0 0 0;
			width: 20px;
			font-size: 20px !important;
		}
	}

	&:not(.hidden) + .risk-module {
		border-top: none;
		margin-top: -1em;
	}
}