@charset "UTF-8";

@font-face {
	font-family: 'DaimlerCS-Regular';
	font-style: normal;
	src:
		url('../../daimler/fonts/woff/DaimlerCS-Regular.woff') format('woff'),
		url('../../daimler/fonts/woff2/DaimlerCS-Regular.woff2') format('woff2');
}

@font-face {
	font-family: 'DaimlerCS-Regular';
	font-weight: bold;
	src:
		url('../../daimler/fonts/woff/DaimlerCS-Bold.woff') format('woff'),
		url('../../daimler/fonts/woff2/DaimlerCS-Bold.woff2') format('woff2');
}

@font-face {
	font-family: 'DaimlerCS-Regular';
	font-style: italic;
	src:
		url('../../daimler/fonts/woff/DaimlerCS-Italic.woff') format('woff'),
		url('../../daimler/fonts/woff2/DaimlerCS-Italic.woff2') format('woff2');
}

@font-face {
	font-family: 'DaimlerCS-Regular';
	font-weight: thin;
	font-style: normal;
	src:
		url('../../daimler/fonts/woff/DaimlerCS-Light.woff') format('woff'),
		url('../../daimler/fonts/woff2/DaimlerCS-Light.woff2') format('woff2');
}

$body-font-family: 'DaimlerCS-Regular', sans-serif;
$colour-primary: #00677F; // Petrol
// $colour-base: #A6CAD8; // Petrol 20%
$colour-base: #E6E6E6; // Light grey
$colour-menu-lines: #c8c8c8; 
$colour-error: #71180C; // Deep red
$colour-accent: #00677F; // Petrol
$colour-light-grey-plus-80-k: #444444; // Light Grey +80K
$colour-toolbar: $colour-light-grey-plus-80-k;

$arrow-down-white: "";

$colour-lines: #e6e6e6;

$button-text-colour: white;
$button-background-colour: black;

$colour-info: #a6cad8;
$colour-warning: #e69123;
$colour-success: #6ea046;

body {
	#toolbar {
		// background: url(brands/daimler/daimler-brush.jpg);
		background-size: cover;
		background-position: center;

		a,
		* {
			color: white;
		}
	}

	.context-menu {
		.context-menu-label.iconified.pat-button {

			&:after {
				background-image: url(data:image/svg+xml;base64,#{$arrow-down-white});
			}

			&.collapsible-open {
				background-color: $colour-accent;

				&:before {
					color: white;
				}
			}
		}
	}

	div.pat-modal {

		&.max-height {
			.panel-content {
				.panel-body {
				    bottom: 59px;
				}

				.wizard-box {
				  	.panel-footer {
				  		padding: 0.5em 1.5em;
				  	}
				}
			}
		}

		.panel-content {
			padding: 2.25em;

		  .wizard-box {
		  	.panel-footer {
		  		// border-top: 1px solid $colour-lines;
		  		height: 2.25em;
		  		background: none;
		  		padding: 0.5em 0 0;

		  		@media screen and (max-width: 768px) {
		  			padding: 0.5em 1.5em;
		  			height: 59px;
		  			
		  			-webkit-box-sizing: border-box;
		  			-moz-box-sizing: border-box;
		  			-ms-box-sizing: border-box;
		  			-webkit-box-sizing: border-box;
		  		}
		  	}
		  }
		}
	}

	.pat-rich {
		.summary {
			font-weight: thin
		}

		h1 {
			font-size: 48px;
			line-height: 52px;
			font-weight: 400;

			@media only screen and (max-width: 768px) {
				font-size: 28px;
				line-height: 36px;
			}
		}

		h2 {
			margin-bottom: 13px;
			font-size: 24px;
			line-height: 30px;
			// font-family: DaimlerCS-Demi;
			padding-top: 52px
		}

		em,
		i {
			font-style: italic;
			color: black;
		}

		strong {
			font-weight: bold;
		}

		ul,
		ol,
		table,
		p,
		dl {
			margin-bottom: 1.15em;
		}
	}

	form {
		fieldset.vertical {
			:not(.pat-checklist) legend,
			:not(.pat-checklist) .legend {
				margin-bottom: 13px;
				font-size: 24px;
				line-height: 30px;
				// font-family: DaimlerCS-Demi;
				// padding-top: 52px
			}
		}
	}

	.pat-well {
		&.info {
			background-color: $colour-base;
		}
	}

	.pat-button {
		// display: inline-block;
		height: 40px;
		line-height: 42px;
		padding: 0 14px;
		text-transform: uppercase;
		font-size: 14px;
		border: none;
		border-radius: 0;

		&:not([disabled]) {
			cursor: pointer;
		}

		&:not([disabled]):hover {
			background: $colour-primary;
		}
	}

	#canvas-toolbar {
		select {
			height: 40px;
			line-height: 42px;
			margin: 0;
			padding-top: 0;
			padding-bottom: 0;
		}
	}

	.pat-button.small,
	.button-bar .pat-button.small,
	.tooltip-container #canvas-toolbar .pat-button {
		line-height: 32px;
	}

	.button-bar,
	.buttonBar {

		.pat-button {
			line-height: 52px;
			height: 49px;
			padding: 0 45px;
		}

	}

	#osc { 

		.pat-button {
			color: white;
		}
		
	}

	#content {
		#main-content {
			&.variation-dashboard {

				overflow: auto;
				background: url(../../daimler/style/daimler-brush.jpg);
				background-size: cover;
				background-position: center center;	

				&:after {
					position: fixed;
					bottom: 40px;
					left: 6%;
					content: ' ';
					display: block;
					background: url(../../daimler/style/Daimler_Office_RGB_100mm.png);
					background-position: center center;	
					background-size: contain;
					background-repeat: no-repeat;
					z-index: 0;
					width: 280px;
					height: 50px;
				}

				#content-pane {
					overflow: visible;
					height: auto;
					
					background-color: $colour-base;
					position: relative;
					z-index: 1;

					@media only screen and (min-width: 320px) {
						margin-bottom: 15rem;
					}

					@media only screen and (min-width: 780px) {
						margin-bottom: 20rem;
					}

					@media only screen and (min-width: 1280px) {
						margin-bottom: 25rem;
					}

					@media only screen and (min-width: 1600px) {
						margin-bottom: 45rem;
					}

					&:before {
						width: 120%;
						height: 1000px; 
						position: absolute;
						background: $colour-base;
						content: ' ';
						display: block;
						bottom: 0;
						right: 0;
						transform: rotate(-10deg);
						transform-origin: bottom right;
						z-index: -1;
					}

					&:after {
						// This construct is in place to guarantee a bottom padding in IE11. IE will under certain circumstances ignore the bottom padding in combination with overflow: auto. The after element replace the bottom padding.

						display: block;
						content: ' ';
						width: 100%;
						height: 1px;
						background: rgba(0,0,0,0);
						clear: both;
					}
					
					#appendix {
						@media only screen and (min-width: 1024px) {
							position: fixed;
							z-index: -2;
							bottom: 40px;
							right: 6%;
						}
					}
				}
			}
		}
	}

	&.osc-on {
		#content {
			#main-content {
				&:after {
					@media only screen and (min-width: 780px) {
						left: 31%;
					}
				}
			}
		}
	}

	// #steps,
	// #osh-target-menu,
	//  {
	// 	position: relative;
	// }
}

