@charset "UTF-8";

.pat-nav-tiles {
	padding: 2em 0;
	
	.item {
		width: 33%;
		color: #666;
		float: left;
		margin: 0 0 4% 0;
		text-align: center;
		height: 270px;

		.no-touchevents & {
			&:hover {
				color: black;

				&:before {
					color: black;
				}
			}
		}

		&:before {
			width: 200px;
			height: 200px;
			background-color: #efefef;
			color: #666;
			line-height: 200px;
			display: block;
			margin: 0 auto 15px;
			text-align: center;
			font-size: 100px;
		}

		@include screen-size(small) {
			height: auto;
			width: 100%;
			float: none;
			display: block;
			background-color: #efefef;
			margin-bottom: 2em;
			padding-bottom: 1.5em;
			padding-top: 1em;

			&:before {
				width: 100%;
				height: 130px;
				line-height: 130px;
			}
		}

	}
}