@charset "UTF-8";

.select2-hidden-accessible {
	position: absolute;
	visibility: hidden;
	-moz-pointer-events: none;
	-webkit-pointer-events: none;
	pointer-events: none;
}

.select2-container {
	.select2-choice { 
		@media screen { background-color: rgba(0,0,0,0.04); }
		@media print { 
			background-color: transparent !important ;
			padding: 0 !important;
		}
	}

	.select2-arrow {
		top: 5px;
		right: 6px;
		position: absolute;
	    cursor: default;

		&:before {
			display: inline-block;
			width: 16px;
			height: 16px;
			content: ' ';
			background-repeat: no-repeat;
		    background-position: right 5px;
		    background-image: url(data:image/svg+xml;base64,#{$arrow-down-dark});
		    background-size: auto 10px;
		}
	}

	abbr.select2-search-choice-close {
  		width: 16px;
  		height: 16px;	
  		background: none;
  		font-size: 16px;
	    cursor: default;

		&:before {
  			display: inline-block;
  			width: 16px;
  			height: 16px;
  			line-height: 16px;
  			content: '#{$glyph-cancel}';
	  		font-family: fontello;
	  		position: absolute;
	  		top: 0;
	  		left: 0;
	  		color: #666;
	  		font-size: 16px;
		    cursor: default;

	  		&:hover {
	  			color: black;
	  		}
		}
	}
}

.select2-container-multi .select2-choices {
	@media screen { background-color: rgba(0,0,0,0.04); }
	@media print { 
		background-color: transparent !important;
		padding: 0 !important;
	}

	.select2-search-field {
		input {
			// width: 48% !important; // Prevents select2 from bleeding under certain circumstances. 
		}
	}
}

.select2-container-multi .select2-choices .select2-search-choice {
	@media print {
		padding: 0 1em 0 0 !important;
		display: block;
		float: none;
		clear: both;
		margin: 0 !important;
		background-color: transparent !important;
		color: black;

		div:before {
			margin-left: 0 !important;
		}

		.select2-search-choice-close {
			display: none;
		}
	}

	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	max-width: 100%;
	@include box-sizing();

	> div {
		display: inline;
	}
}