#screen-intro {
	@include screen-size(medium-large) {
		display: flex;
	}

	@include screen-size(small) {
		@include clear;
		margin-bottom: 15px;	
	}

	.tool-group {
		@include screen-size(medium-large) {
			flex: 1;
		}

		.pat-select {
			@include screen-size(medium-large) {
				float: right;
			}
			@include screen-size(small) {
				float: left;
			}
		}
	}

	.title-group {
		@include screen-size(medium-large) {
			flex: 4;
		}

		h1 {
			margin-bottom: 30px;
			font-size: bold;
		}

		.byline {
			margin-top: -30px;
			margin-bottom: 30px;

			@include screen-size(small) {
				margin-bottom: 15px;			}
		}
	}

}