.pat-grid-list {
    margin-bottom: 1em;
    margin-top: 0;
    float: left;
    width: 100%;
    border-bottom: 1px solid $colour-base;

    dt {
        width: 35%;
        float: left;
        font-weight: bold;
        clear: left;
        border-top: 1px solid $colour-base;
        padding-right: 5%;
        padding-top: 0.2em;
        padding-bottom: 0.2em;
        color: rgba(0,0,0,0.8);
        opacity: 0.6;
    }

    dd {
        width: 60%;
        margin-right: -100%;
        margin-bottom: 0;
        display: inline-block;
        float: left;
        border-top: 1px solid $colour-base;
        padding-top: 0.2em;
        padding-bottom: 0.2em;
        margin-left: 0;

        p {
            margin-bottom: 0.3em;
          
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}