// Primary colours
$colour-primary: #996699 !default;
$colour-secondary: #003399 !default;
$colour-base: #efefef !default;
$colour-alert: red !default;
$colour-accent: #0081be !default;
$colour-success: #CBF08B !default;
$colour-warning: #c16800 !default;
$colour-info: lightblue !default;

$colour-base-font: #222222 !default;
$colour-sidebar: $colour-base !default;
$colour-sidebar-text: #666 !default;
$colour-sidebar-links: black !default;
$colour-toolbar: $colour-primary !default;
$colour-lines: #e6e6e6 !default;
$colour-menu-lines: #c8c8c8 !default; 
$colour-tree-section-active: $colour-base !default;

$colour-button-continue: $colour-primary !default;
$colour-button-continue-text: white !default;

$button-text-colour: black !default;
$button-background-colour: transparent !default;

$border-radii: 10px !default;


// Photoswipe 
$pswp__assets-path: "/style/";

// Form parameters

$form-param-is: "is";
$form-param-equals: "equals";
$form-param-not-equals: "";
$form-param-contains: "";
$form-param-contains-not: "";
$form-param-before: "";
$form-param-after: "";

// Glyps

$glyph-archived: "\e835";
$glyph-checkbox: "\e808";
$glyph-checkbox-active: "\e809";
$glyph-radio-button: "\e807";
$glyph-radio-button-active: "\e806";
$glyph-close: "\e899";
$glyph-eye: "\e8a0";
$glyph-eye-off: "\e8a1";
$glyph-eyedropper: "\f1fb";
$glyph-icon-close: $glyph-close;
$glyph-remove: "\e805";
$glyph-throbber: "\e822";
$glyph-left: "\e811";
$glyph-right: "\e82a";
$glyph-tool: "\e825";
$glyph-risk-assessment: "\e826";
$glyph-help-circle: "\e800";
$glyph-tree: "\f1bb";
$glyph-menu: "\f0c9";
$glyph-ellipsis: "\e865";
$glyph-ellipsis-vertical: "\f142";
$glyph-menu-bars: "\e830";
$glyph-sidebar: "\e81c";
$glyph-icon-user: "";
$glyph-icon-ellipsis: "\e865";
$glyph-icon-user-group: "";
$glyph-mail: "\e81f";
$glyph-picture: "\e842";
$glyph-list-item: "\e82a";
$glyph-ok-circled: "\e80a";
$glyph-cancel-circled: "\e805";
$glyph-cancel: "\e805";
$glyph-involve: "\f0e6";

// Grid
$grid-gutter: 1.5rem;

// Background symbols
$arrow-down-dark: "";
$arrow-down-white: "";

// Font treatment
$base-font-size: 18px;
$base-line-height: 1.4;
$font-weight-normal: 300;
$font-weight-bold: bold;

// We use these to control various global styles
$body-bg: white !default;
$body-font-color: #222222 !default;
$body-font-family: 'Myriad Pro', sans-serif !default;
$body-font-weight: $font-weight-normal !default;
$body-font-style: normal !default;

$base-space: 1em; // Create universal white space (e.g. margin, padding, left)

$sidebar-left-padding: 6% !default;
$sidebar-right-padding: 6% !default;
$sidebar-top-padding: 1.7rem !default;

// Responsive variables

$break-small: 768px;
$break-medium: 769px;
$break-large: 1280px;

$padding-small-screen: 20px;

