.download-blocks {
	.download-block {
		margin-bottom: 1.5em;

		@include screen-size(small) {
			height: auto !important;
			margin-bottom: 0.5em !important;
			clear: both;
		}

		.pat-well {
			height: 100%;
			-moz-box-sizing: border-box;
			-webkit-box-sizing: border-box;
			box-sizing: border-box;

			padding: 10% 5%;
			line-height: 1.5em;

			font-size: 0.8em;

			.button-bar {
				text-align: center;
				margin-bottom: 0;
			}

			.explanation {
				text-align: center;
			}

			.pat-button {
				font-size: 1.2em;
				@include screen-size(small) {
					font-size: 1em;
				}
				width: 80%;
				display: inline-block;
				margin: 0 auto 1em;
				padding: 0.5em 0.2em 0.4em;
				background: rgba(0,0,0,0.1);
				text-decoration: none;

				&:hover {
					text-decoration: underline;
				}

				&:before {
					font-family: fontello;
					content: "\e819 ";
					text-decoration: none;
					color: $colour-accent;
				}
			}
		}
	}

	.column {
		-moz-box-sizing: border-box;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}

	&.count-4 {
		.column {
			width: 50%;
		}
	}

	&.count-3 {
		.column {
			width: 50%;
		}

		.column:last-child {
			width: 100%;
		}
	}

	&.count-2 {
		.column {
			width: 50%;
		}
	}

	&.count-1 {
		.column {
			width: 100%;
		}
	}
}