.ueber-back-button {
	display: inline-block;
	line-height: 40px;
	height: 40px;
	background: #efefef;
	color: #666;
	border-radius: 5px;
	font-size: 18px;
	padding: 0 2em 0 1.5em;
	position: relative;
	margin-left: 19px;

	&:before {
		content: ' ';
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 18px 20px 18px 0;
		border-color: transparent #efefef transparent transparent;
		position: absolute;
		left: -19px;
		top: 2px;
	}

	&:hover {
		color: black;
	}
}