@charset "UTF-8";

.canvas-toolbar {
	@include clear;
	margin-bottom: 1em;
	// height: 2em;
	line-height: 2em;
	padding-bottom: 1em;

	@media print { display: none; }

	.title {
		float: left;

		.tooltip-container & {
			font-size: 20px;
			line-height: 1.5em;
		}
	}

	.bar-title {
		margin: 0 1em 0 0;
		float: left;
	}

	.pat-button,
	.context-menu {
		&.float-after {
			float: right;
			margin-right: 0;
			margin-left: 8px;
		}
		
		.tooltip-container & {
		    font-size: 0.6em;
		    padding: 0 0.3em;
		    line-height: 1.4em;
		}
	}

	.context-menu {
		.context-menu-label.iconified.pat-button {
			float: right;
			width: 60px;
			margin: 0;

			&:before {
				transform: none !important;
				font-family: fontello;
				content: '#{$glyph-ellipsis}';
				line-height: 40px;
				left: 9px;
			}

			&:after {
				color: white;
				position: absolute;
				top: 50%;
				margin-top: -7px;
				right: 10px;
			}
		}

		.panel-content {
			font-size: $base-font-size;
		}
	}

	@include screen-size(small) {
		padding: 0;
		// margin: 0;
	}

	.float-after {
		margin-left: 2em;
		margin-right: 0;
	}

	label.pat-checklist {
		&:hover {
			color: black;
		}
	}

	> .pat-select,
	> .pat-select.bare,
	> .context-menu,
	> label.pat-checklist {
		display: inline-block;
		float: none;
		width: auto;
		color: #666;

		// &:not(.has-versions) {
		// 	pointer-events: none;

		// 	&:after {
		// 		background: none;
		// 	}
		// }

	    @include screen-size(medium-large) { 
	      margin-right: 1em;
	    }

	    @include screen-size(small) { 
	      // display: block;
	      // margin-bottom: 1em;
	      // display: none !important;
	      margin-right: 0.6em;
		  margin-bottom: 0.5em;
	    }
	}

	input[type="search"] {
		float: right;
		border-radius: 1em;
		background: white;
		border: 1px silver solid;
	    @include screen-size(small) { 
	      width: 100%;
	      margin-bottom: 1.5em;
	      margin-top: 0.5em;
	    }
	}

}