$advanced-search-animation-speed: 0.4s;
$container-padding: 15px;
$colour-search-box: black;
$colour-search-box-text: black;


.advanced-search {
  overflow-x: hidden;
  overflow-y: visible;

  fieldset.advanced {
    margin-bottom: 1em;
  }

  .search-rule {
    margin: 0;
    // overflow-x: hidden;
    // overflow-y: visible;
    transition: all $advanced-search-animation-speed;
    clear: both;
    position: relative;

    &.row-1 {
      height: 2em;
    }

    .view-compact & {
      height: 4em;

      &:not(.row-1) {
        display: none;
      }
    }

    select,
    input {
      @include box-sizing;
      margin: 0;
      height: 2em;
      line-height: 2em;
      padding: 0 0.5em;
      width: 100%;
      float: left;
      color: black;
      font-weight: normal;
    }

    .and-or.pat-select:after {
      .header.home & {
        background-image: url(data:image/svg+xml;base64,);
        color: white;
      }
    }

    [data-option-value="lacks"] select {
      background-image: url(data:image/svg+xml;base64,);
      color: white;
    }

    .and-or.pat-select.bare {
      width: 100%;
      // text-align: center;
      margin: 1em auto;
      display: inline-block;
      padding: 0 15px;
      color: black;
      font-size: 0.8em;

      .header.home & {
        color: white;

        &:after {
          color: white;
        }
      }

      select {
        width: auto;
        right: inherit;
        color: black;

        .header.home & {
          color: white;
        }
      }
    }

    .field-choice {
      width: 30%;
      overflow: hidden;
      transition: all $advanced-search-animation-speed;
      float: left;

      select {
        width: 93%;
      }

      .view-compact & {
        width: 0 !important;
        margin-left: -100%;
        opacity: 0;
      }

      &[data-option-value="SearchableText"] ~ .parameters.lemma,
      &[data-option-value="searchable-text"] ~ .parameters.lemma,
      &[data-option-value="Title"] ~ .parameters.lemma,
      &[data-option-value="title"] ~ .parameters.lemma,
      &[data-option-value="content"] ~ .parameters.lemma,
      &[data-option-value="Description"] ~ .parameters.lemma,
      &[data-option-value="description"] ~ .parameters.lemma,
      &[data-option-value="author"] ~ .parameters.lemma,
      &[data-option-value="sector"] ~ .parameters.lemma,
      &[data-option-value="contractor"] ~ .parameters.lemma,
      &[data-option-value="plant"] ~ .parameters.plant,
      &[data-option-value="created"] ~ .parameters.date,
      &[data-option-value="effective"] ~ .parameters.date,
      &[data-option-value="modified"] ~ .parameters.date,
      &[data-option-value="department"] ~ .parameters.department {
        display: block;
      }
    }

    .parameters {
      width: calc(70% - 3em);
      transition: width $advanced-search-animation-speed;
      float: left;
      position: relative;
      display: none;

      .view-compact & {
        width: 100% !important;
        font-size: 1.93em;
      }

      [data-option-value="contains"] select,
      [data-option-value="ilike"] select {
        background-color: lightgreen;
      }

      [data-option-value="contains-not"] select,
      [data-option-value="notilike"] select {
        background-color: orange;
      }

      [data-option-value="#{$form-param-equals}"] select,
      [data-option-value="#{$form-param-is}"] select,
      [data-option-value="__eq__"] select {
        background-color: lightblue;
      }

      > .pat-select {
        float: left;
        width: 30%;
        margin: 0;
        @include box-sizing;

        transition: all $advanced-search-animation-speed;

        .view-compact & {
          margin-left: -100%;
          opacity: 0;
        }
      }

      input,
      .select2-container {
        width: 70%;
        border: none;
        padding: 0 0.8em;
        margin: 0;
        border: none;
        z-index: 4;
        box-shadow: none;
        color: $colour-search-box;
        transition: all $advanced-search-animation-speed;
        position: absolute;
        top: 0;
        left: 30%;
        @include box-sizing;
        color: black;
        background-color: white;
        // left: calc(30% + 6px);

        .header.home & {
          background: white;
        }

        .view-compact & {
          width: calc(100% - 18rem);
          height: 62px;
          font-size: 0.95em;
          left: 15px;

          &.lemma-field {
            opacity: 0;
            pointer-events: none;
          }
        }
      }

      .select2-container {
        padding: 0;

        .select2-choice {
          padding: 0 0.8em;
          line-height: 44px;
          background-color: transparent;

        }
      }
    }

    .remove-clone {
      border: none;
      background: none;
      width: 1.5em;
      height: 2em;
      overflow: hidden;
      margin: 0;
      padding: 0;
      position: absolute;
      right: 15px;
      text-indent: -1000px;
      top: 48px;

      &:hover:before {
        color: black;
      }

      &:active {
        box-shadow: none !important;
        outline: none;

        &:before {
          color: $colour-accent;
        }
      }

      &:before {
        font-family: fontello;
        content: '#{$glyph-remove}';
        color: rgba(0,0,0,0.5);
        position: absolute;
        right: 0;
        text-indent: -1em;

        .header.home & {
          color: white;
        }
      }
    }
  }

  .button-bar {
    overflow: hidden;
    transition: all $advanced-search-animation-speed;
    height: 2em;
    margin: 0;

    .header.home .view-extended & {
      margin-bottom: 2em;
    }

    .view-compact & {
      height: 0;
      margin-bottom: 0;
      opacity: 0;
    }

    // .pat-button.icon-search {
    //   color: white;
    // }
  }
}
