.presentation-slide {
	margin-bottom: 1em;
	transition-delay: 1s;
	transition: opacity 1s;
	cursor: default;

	.auto-scale-wrapper {
		// Styling on auto scale wrapper to patch background bleeding issue on iPad OS 13.

		overflow: hidden;
		outline: 1px silver solid;
	}

	@mixin fullscreen {
		width: 100%;
		height: 100%;
		background-color: black;

		.auto-scale-wrapper {
			background-color: white;
			top: 50%;
			left: 50%;
			position: relative;
			transform: translate(-50%,-50%);
		}

		.slide-nav-next,
		.slide-nav-prev {
			display: block;
		}	
	}

	.slide-nav-next,
	.slide-nav-prev {
		position: absolute;
		top: 50%;
		width: 50px;
		height: 300px;
		line-height: 300px;
		background-color: black;
		margin-top: -150px;
		overflow: hidden;
		color: transparent;
		display: none;

		&:-ms-fullscreen {
			display: block;
		}

		&:hover {
			&:before {
				color: white;
			}
		}

		&:before {
			display: block;
			height: 100%;
			width: 100%;
			content: " ";
			font-family: fontello;
			color: gray;
			font-size: 50px;
			text-align: center;
		}
	}

	&:-webkit-full-screen {
		@include fullscreen;
	}

	&:-ms-fullscreen {
		@include fullscreen;
	}

	&:fullscreen {
		@include fullscreen;
	}

	.slide-nav-next {
		right: 0;

		&:before {
			content: '#{$glyph-right}';
		}
	}

	.slide-nav-prev {
		left: 0;

		&:before {
			content: '#{$glyph-left}';
		}
	}

	.pat-collapsible & {
		opacity: 0;
	}

	.pat-collapsible.open & {
		animation: fadeIn 1s;
		animation-delay: 0.2s;
		animation-fill-mode: forwards;
	}

	.slide-content {
		width: 1920px;
		height: 1080px;

		.slide-title {
			font-size: 70px;
			padding: 50px 99px 5px;
			line-height: 1.15;
			box-sizing: border-box;
			height: 219px;
			font-weight: normal;
			overflow: hidden;
			margin-bottom: 20px;
		}

		.toggle {
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			position: absolute;
			background-color: rgba(255,255,255,0.8);
			background-color: rgba(0,0,0,0.4);
			transition: opacity 0.5s;
			color: transparent;
		}

		&.mode-view {
			.toggle {
				display: none;
			}

			.training-notes-preview {
				display: block;
			}

			.content-mirror {
				opacity: 0;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				z-index: 10;
			}
		}

		&.mode-edit {
			.toggle {
				animation: fadeIn 2s;	
				z-index: 1;
			}

			.training-notes-preview {
				display: none;
			}

			.content-mirror {
				opacity: 1;
			}
		}

		&.mode-view {
			&:hover {				
				.training-notes-preview:not(.injecting):before {
					position: absolute;
					z-index: 1;
					top: -10px;
					right: -10px;
					bottom: -10px;
					left: -10px;
					outline: 4px solid lightblue;
					content: ' ';
					display: block;
				}
			}
		}

		.slide-body {
			font-size: 48px;
			padding: 12px 99px 0;
			margin-top: -12px;
			line-height: 64px;
			height: 777px;
			overflow: hidden;
			position: relative;

			.button-bar {
				.pat-button {
					line-height: 72px;
					height: 69px;
					font-size: 48px;
					vertical-align: middle;
				}
			}

			h1,
			h2,
			h3,
			h4,
			h5 {
				font-size: 50px;
				font-weight: bold;
				margin-bottom: 62px;
				line-height: 64px;
				padding: 0;
			}

			blockquote {
				padding-left: 20px;
				border-left: 10px silver solid;
				color: #666;
				font-style: italic;
			}

			ul,
			ol,
			table,
			p,
			dl {
				margin-bottom: 62px;
			}

			h4,
			h5 {
				margin-bottom: 0;
			}	

			.content-mirror {
				margin-bottom: 12px;				

				.notes,
				.notes + textarea {
					line-height: 64px;
					padding-bottom: 50px;
					margin: 0;
					color: black;
				}

				.notes .output {
					margin-bottom: 50px;
				}
			}

			.training-notes-preview {
				position: relative;

				em.placeholder {
					font-style: italic;
					color: silver;
				}
			}
		}

		.slide-footer {
			border-top: 2px solid silver;
			color: black;
			position: absolute;
			left: 99px;
			right: 99px;
			bottom: 0;
			height: 57px;
			white-space: nowrap;
			line-height: 54px;
			font-size: 24px;

			.organisation-name {
				float: left;

				strong {
					font-weight: normal;
				}
			}

			.colofon {
				float: right;
			}
		}

		// Templates

		&.template-certificate {
			.certificate {
				transform: scale(0.4);
				transform-origin: top right;
				position: absolute;
				// top: 115px;
				top: 225px;
				right: 99px;
				box-shadow: 0 10px 50px rgba(0,0,0,0.2);
				border: 8px white solid;
			}

			.slide-body {
				margin-top: 40px;
				height: 1000px;

				.pat-checklist {
					font-size: 0.8em;
					font-size: 0.8em;
					line-height: 1.1;
					margin-bottom: 70px;
				}
			}

			.button-bar {
				position: absolute;
				bottom: 80px;
				left: 154px;
				right: 100px;
			}

			.information {
				position: absolute;
				// width: 780px;
				width: 1020px;
				// height: 780px;
				height: 949px;
				overflow: hidden;
				left: 99px;
				top: 0;

				figure {
					width: 100%;
					margin-bottom: 0.8em;

					img {
						width: 100%;
					}
				}
			}
		}

		&.template-failed {
			.slide-body {
				margin-top: 219px;
			}
		}

		&.template-cover {
			.slide-title {
				position: absolute;
				top: 0;
				right: 0;
				left: 0;
				color: white;
				padding: 340px 99px 290px;
				background-image: url(/media/daimler-slide-brush.png);
				background-position: bottom;

				&:before {
					content: ' ';
					background-image: url(/media/Daimler_Office_RGB_055mm.png);
					background-size: contain;
					background-repeat: no-repeat;
					width: 535px;
					height: 80px;
					position: absolute;
					top: 126px;
					left: 99px;
				}
			}
		}

		&.template-single-illustration {
			.training-notes,
			.description,
			.existing-measures {
				width: 875px;
			}

			.information {
				position: absolute;
				width: 750px;
				height: 780px;
				overflow: hidden;
				right: 99px;
				top: 0;

				figure {
					width: 100%;
					margin-bottom: 0.8em;

					img {
						width: 100%;
					}
				}
			}
		}

		&.template-two-column {
			.column-1 {
				position: absolute;
				width: 780px;
				height: 780px;
				overflow: visible;
				left: 99px;
				top: 0;
			}

			.column-2 {
				width: 780px;
				height: 780px;
				overflow: visible;
				float: right;
				clear: both;
				position: relative;

				figure {
					width: 100%;
					margin-bottom: 0.8em;

					img {
						width: 100%;
					}
				}

				.training-notes {
					position: relative;
					margin: -16px;
					padding: 16px;
					overflow: hidden;
					height: 276px;

					font-size: 35px;
					line-height: 1.3;
					color: gray;
					font-style: italic;

					.output,
					.pat-content-mirror {
						font-size: 35px;
						line-height: 1.3;
						color: gray;
						font-style: italic;
					}

					// .content-mirror {
					// 	max-height: 349px;
					// }
				}
			}
		}

		&.template-measures {
			&.mode-view {
				.edit-measures {
					display: none;
				}
			}

			&.mode-edit {
				.measure-list {
					display: none;
				}
			}

			.measure-list {
				margin: -16px;
				padding: 16px;
				position: relative;
			}

			&:hover {
				.measure-list {
					outline: 4px solid lightblue;
				}
			}

			.edit-measures {
				background-color: white;
				padding: 16px;
				margin: -16px;
				position: relative;
				z-index: 1;
				max-height: 695px;
				overflow: auto;

				&.pat-checklist {
					label {
						color: gray;

						&.checked {
							color: black;

							&:before {
								content: '#{$glyph-eye}';
							}
						}

						&.unchecked {
							// text-decoration: line-through;

							&:before {
								content: '#{$glyph-eye-off}';
								text-decoration: none;
							}
						}
					}
				}
			}
		}

		&.template-module {
			background-size: cover;
			background-color: #E6E6E6;
			background-position: center;

			.slide-title {
				height: auto;
				color: white;
			}

			.slide-body {
				height: auto;
				color: white;
				overflow: visible;

				&:before {
					content: ' ';
					background-image: url(/media/daimler-slide-brush.png);
					background-position: bottom;
					width: 100%;
					height: 1080px;
					position: absolute;
					bottom: -140px;
					left: 0;
					right: 0;
					z-index: -1;
				}
			}
		}
	}
}