@charset "UTF-8";

.portlet {
	&.height-full {
		@include screen-size(medium-large) {
			min-height: 100%;
		}

		// .content {
		// 	height: 100%;
		// }
	}

	@include screen-size(small) {
		width: calc(100% + 2 * #{$padding-small-screen});
    	margin-left: -1 * $padding-small-screen;
    	margin-right: -1 * $padding-small-screen;
	}

	.content {
		position: relative;
		border: 1px solid #dedede;
		background-color: white;

		@include screen-size(medium-large) {
			border-radius: 10px;
		}

		.context-menu .panel-content {
			width: 400px;
		}

	    .portlet-intro {
			display: flex;
			padding: 6% 6% 20px;

			.portlet-tool-group {
				flex: 1;

				> .pat-select,
				.context-menu {
					float: right;
				}
			}

			.portlet-title-group {
				flex: 4;

				h1 {
					margin-bottom: 30px;
					font-size: bold;
				}

				.byline {
					margin-top: -30px;
					margin-bottom: 30px;
				}
			}

		}

		.portlet-body {
			padding: 0 6% 6%;
			overflow: auto;

			.portlet-toolbar {
				margin-bottom: 1em;
				@include clear;

				.pat-select {
					float: left;
				}
			}

			.separation-title {
				font-size: 1.1em;
				margin: 0 0 16px 0;
			}

			.training {
				padding-top: 1em;
				
				.auto-scale-wrapper {
					overflow: hidden;
				}

				.training-title {
					margin-bottom: 0.5em;
				}

				.certificate-link {
					margin-bottom: 1em;
					float: left;

					* {
						cursor: pointer;
					}
				}
			}

			.certificates {
				.auto-scale-wrapper {
					overflow: hidden;
					margin-bottom: 1em;
				}

				a.certificate-link {
					float: left;

					* {
						cursor: pointer;
					}
				}
			}
		}

	    @include screen-size(small) {
	    	padding: 0;
	    	border: none;
	    	// background-color: transparent;
	    }
	}



	margin-bottom: 2rem;

	.portlet-logo {
		text-align: center;
		margin-bottom: 1.4em;

		img {
			@include screen-size(small) {
				max-width: 90%;
			}
			@include screen-size(medium-large) {
				max-width: 70%;
			}
		}
	}

	.portlet-title {
		margin: 0 0 1rem 0;
		font-size: 1.2;
		font-weight: 300;

		@include screen-size(medium) {
			font-size: 1;
		}

		a:hover {
			text-decoration: underline;
		}

		a:before {
			font-family: fontello;
			font-size: 0.7em; 
			margin-right: 0.8em;
			display: inline-block;
			vertical-align: middle;
			margin-top: -0.4em;
			width: 2em;
			height: 2em;
			text-align: center;
			line-height: 2.1em;
			border-radius: 2px;
			background-color: $colour-accent;
			color: white;
		}

		sup {
			font-size: 0.6em;
			color: gray;
		}
	}

	.link {
		// padding: 0.2em 0;
		
		&:hover {
			text-decoration: none;
		}
	}

	.portlet-footer {
		padding: 0 6% 6%;
		// font-size: 0.8em;

		.more-link {

			&:hover {
				text-decoration: underline;
			}
		}
	}

	.title {
		margin: 0;
		font-size: 1em;
		font-weight: normal;
	}

	.byline {
		color: #666;
		font-size: 0.8em;
		line-height: 1.3em;
		font-style: italic;
		margin: 0;
	}

	table {
		td {
			&.date-finished {
				width: 20%;
			}
		}
	}

	.pat-rich {
		table {
			a {
				text-decoration: none;

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

}
